<template>
  <modal v-if="open" @close="$emit('close')" class="modal-contract">
    <template slot="header">
      <h4 class="modal-title">  {{ isSaveMode ? "" : "" }}</h4>
    </template>

    <template slot="body" v-if="loading">
      <spinner />
    </template>
    <template slot="body" v-else>
      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input v-model="contract.name" type="text" />
          <span class="md-helper-text"> .</span>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input v-model="contract.addr" :disabled="true" type="text" />
          <md-button class="md-default md-dense" @click="kakaoMap">
            <template></template>
          </md-button>
        </md-field>
        <md-field v-if="isSaveMode">
          <label> </label>
          <md-input v-model="contract.detail" type="text" />
          <span class="md-helper-text"> .</span>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input v-model="contract.tel" type="text"></md-input>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input v-model="contract.contractor" type="text"></md-input>
          <span class="md-helper-text"> .</span>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input :disabled="true" />
          <md-button class="md-default md-dense  md-fileinput">
            <template></template>
            <input
              multiple
              accept="image/*"
              type="file"
              name="circle"
              @change="onMenuFileChange"
              capture="camera"
            />
          </md-button>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input :disabled="true" />
          <md-button class="md-default md-dense  md-fileinput">
            <template></template>
            <input
              multiple
              accept="image/*"
              type="file"
              name="circle"
              @change="onMenuFileChange"
              capture="camera"
            />
          </md-button>
        </md-field>
      </div>

      <div class="md-layout">
        <md-field>
          <label></label>
          <md-input :disabled="true" />
          <md-button class="md-default md-dense  md-fileinput">
            <template></template>
            <input
              multiple
              accept="image/*"
              type="file"
              name="circle"
              @change="onMenuFileChange"
              capture="camera"
            />
          </md-button>
        </md-field>
      </div>

      <div class="md-layout" v-if="isReSendStatus">
        <md-field>
          <label></label>
          <md-textarea :disabled="true" v-model="contract.textarea" />
        </md-field>
      </div>
    </template>

    <template slot="footer">
      <md-card-actions md-alignment="space-between">
        <div>
          <md-button @click="close" class="md-default md-dense">
            
          </md-button>
        </div>
        <div v-if="isSaveStatus || isSaveMode">
          <md-button @click="save" class="md-info md-dense">
            
          </md-button>
          <md-button @click="send" class="md-success md-dense">
            
          </md-button>
        </div>
        <div v-else-if="isSendStatus">
          <md-button class="md-success md-dense">
            
          </md-button>
          <md-button class="md-danger md-dense" @click.native="dis">
            
          </md-button>
        </div>
        <div v-else-if="isReSendStatus">
          <md-button class="md-success md-dense">
            
          </md-button>
        </div>
      </md-card-actions>
    </template>
  </modal>
</template>

<script>
import Swal from "sweetalert2";
import { axiosInstance } from "@/axiosModule";
import Modal from "@/components/Modal";
import Spinner from "@/components/Spinner";

export default {
  components: { Spinner, Modal },
  props: {
    id: {
      type: Number
    },
    open: {
      type: Boolean
    }
  },
  watch: {
    id: async function(id) {
      if (id > -1) {
        this.loading = true;

        const { data } = await axiosInstance.get(
          `https://my-json-server.typicode.com/dslim0226/test-json/contract/${id}`
        );

        this.contract = { ...this.contract, ...data };
        this.loading = false;
      }
    }
  },
  data: () => ({
    contract: {
      id: 0,
      name: "",
      addr: "",
      detail: "",
      tel: "",
      contractor: "",
      menu: [],
      business: [],
      contract: [],
      status: "",
      textarea: "   ."
    },
    loading: false
  }),
  computed: {
    isSaveMode() {
      return this.id === -1;
    },
    isSaveStatus() {
      return this.contract.status === "";
    },
    isSendStatus() {
      return this.contract.status === "";
    },
    isApplyStatus() {
      return this.contract.status === "";
    },
    isReSendStatus() {
      return this.contract.status === "";
    }
  },
  methods: {
    close() {
      this.contract = {
        id: 0,
        name: "",
        addr: "",
        detail: "",
        tel: "",
        contractor: "",
        menu: [],
        business: [],
        contract: [],
        status: "",
        textarea: "   ."
      };

      this.$emit("close");
    },
    save() {
      Swal.fire({
        title: ` `,
        text: " .",
        confirmButtonClass: "md-button md-success",
        type: "success"
      }).then(() => {
        this.close();
      });
    },
    send() {
      Swal.fire({
        title: ` `,
        text: " .",
        confirmButtonClass: "md-button md-success",
        type: "success"
      }).then(() => {
        this.close();
      });
    },
    kakaoMap() {
      const hello = this.hello;
      this.$loadScript(
        "//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js"
      )
      .then(() => {
        new daum.Postcode({
          oncomplete: function(data) {
            hello(data.zonecode, data.jibunAddress);
          }
        }).open();
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          text: `       .<br>  .`
        });
      });
    },
    hello(zip, addr) {
      this.addr = `(${zip}) ${addr}`;
    },
    async onMenuFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;

      this.menu = files;

      // TODO : S3   

      // const multipartFile = new FormData();

      // for (let i = 0; i < files.length; i++) {
      //   multipartFile.append(`files`, files[i]);
      // }
      //
      // for (const key of multipartFile.entries()) {
      //   console.log(`${key}`);
      // }
      //
      // const hi = await axiosInstance.post(
      //   "/private/contract",
      //   multipartFile,
      //   {
      //     headers: {
      //       "Content-Type": "multipart/form-data"
      //     }
      //   }
      // );
    },
    async onBusinessFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;

      this.business = files;

      // TODO : S3   
    },
    async onContractFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;

      this.contract = files;

      // TODO : S3   
    },
    async dis() {
      Swal.fire({
        title: ` .`,
        input: "textarea",
        inputLabel: "Message",
        showCancelButton: true
      }).then(() => {
        Swal.fire({
          title: ` `,
          text: "  .",
          confirmButtonClass: "md-button md-success",
          type: "success"
        }).then(() => {
          this.$router.push({ path: "/list/contract" });
        });
      });
    }
  }
};
</script>
